<template>
  <div class="btn" :style="styleObject" @click="handleClick">
    <slot></slot>
  </div>
</template>

<script>
export default {
  name: "rect-button",
  props: {
    width: { type: String, default: "120px" },
    disabled: { type: Boolean, default: false }
  },
  computed: {
    styleObject() {
      if (this.disabled) {
        return {
          width: this.width || "120px",
          cursor: "auto",
          backgroundColor: "#fff",
          color: "#9CA3AF"
        };
      } else
        return {
          width: this.width || "120px",
          cursor: "pointer"
        };
    }
  },
  methods: {
    handleClick() {
      if (!this.disabled) {
        this.$emit("click");
      }
    }
  }
};
</script>

<style lang="scss" scoped>
