<template>
  <div class="header">
    <div class="main">
      <div class="left hidden-md-and-down" @click="toHome">
        <img
          class="logo"
          src="../assets/img/favicon.png"
          alt="AUtomata"
          @click="toHome"
        />
        <div class="title">Witness</div>
      </div>
      <div class="top-title">
        <slot class="left" name="left"></slot>
        <transition appear appear-active-class="fade"
          ><slot class="center"></slot
        ></transition>
        <slot calss="right" name="right"></slot>
      </div>
      <rect-button
        class="connect hidden-md-and-down"
        @click="showWalletDialog = true"
        width="180px"
      >
        {{ wallet }}
      </rect-button>
    </div>
    <connect-dialog
      :showDialog="showWalletDialog"
      @onClose="showWalletDialog = false"
    />
    <float-button />
  </div>
</template>

<script>
import ClipboardJS from "clipboard";
import RectButton from "./RectButton.vue";
import FloatButton from "./FloatButton.vue";
import ConnectDialog from "./ConnectDialog.vue";
import { mapGetters } from "vuex";

export default {
  components: {
    RectButton,
    FloatButton,
    ConnectDialog
  },

  data() {
    return {
      showWalletDialog: false,
      copied: false
    };
  },
  mounted() {
    this.$store.dispatch("getAccount");
    this.$store.dispatch("getVoteHistory");
  },
  computed: {
    wallet: function() {
      if (!this.account || this.account === "" || !this.connected) {
        return "Connect wallet";
      } else {
        let length = this.account.length;
        return (
          this.account.substring(0, 6).toUpperCase() +
          "..." +
          this.account.substring(length - 4, length).toUpperCase()
        );
      }
    },
    isSmallScreen() {
      return window.innerWidth < 450;
    },

    ...mapGetters(["account", "connected", "accountVotes", "voteHistory"])
  },
  methods: {
    toHome() {
      if (this.$route.name !== "home") this.$router.push({ path: "/" });
      else {
        this.$router.go();
      }
    },
    async copyAddress() {
      let clipboard = new ClipboardJS(".btn");
      let that = this;
      clipboard.on("success", function(e) {
        console.debug(e);
        that.copied = true;
      });
      setTimeout(() => (this.copied = false), 5000);
    },
    async connectWallet() {
      this.showWalletDialog = false;
      await this.$store.dispatch("connectWallet");
    }
  }
};
</script>

