<template>
  <el-dialog
    :title="connected ? 'Connected wallet' : 'Connect to a wallet'"
    center
    style="font-weight: bold"
    :visible.sync="dialogVisiable"
    :modalAppendToBody="false"
    width="500px"
    :fullscreen="isSmallScreen"
  >
    <div class="dialog">
      <rect-button width="100%" v-show="!connected" @click="connectWallet">
        MetaMask
      </rect-button>
      <div class="item" v-show="connected">
        <div class="title">MetaMask</div>
        <div class="account">
          <div class="wallet">{{ wallet }}</div>
          <div class="right">
            <transition name="slide-fade" v-if="copied"
              ><div class="copy">Copied!</div></transition
            >
            <div class="copy" v-else>Copy Address</div>
            <el-button
              class="copy-btn"
              size="mini"
              @click="copyAddress"
              :data-clipboard-text="account"
            >
              <i class="el-icon-document-copy"></i>
            </el-button>
          </div>
        </div>
        <div class="votes" v-show="accountVotes.length > 0">
          <div
            class="vote"
            v-for="(record, index) in accountVotes"
            :key="index"
          >
            <div class="vote-title">
              {{ record.title }}
            </div>
            <div class="vote-date">
              {{ dateString(record.date / 1000) }}
              <i class="el-icon-success"></i>
            </div>
          </div>
        </div>
      </div>
    </div>
  </el-dialog>
</template>

<script>
import ClipboardJS from "clipboard";
import RectButton from "./RectButton.vue";
import { mapGetters } from "vuex";

export default {
  props: {
    showDialog: { type: Boolean, default: false }
  },
  components: {
    RectButton
  },
  data() {
    return {
      copied: false
    };
  },
  methods: {
    async copyAddress() {
      let clipboard = new ClipboardJS(".copy-btn");
      let that = this;
      clipboard.on("success", function(e) {
        console.debug(e);
        that.copied = true;
      });
      setTimeout(() => (this.copied = false), 5000);
    },
    async connectWallet() {
      this.showWalletDialog = false;
      await this.$store.dispatch("connectWallet");
    },
    dateString(timestamp) {
      const date = new Date(parseInt(timestamp) * 1e3);
      let dateString = date.toLocaleString().split(",")[0];
      if (dateString === "Invalid Date") {
        dateString = "Unknown";
      }
      return dateString;
    }
  },
  computed: {
    dialogVisiable: {
      get: function() {
        return this.showDialog;
      },
      set: function(value) {
        this.copied = false;
        this.$emit("onClose", false);
        return value;
      }
    },
    wallet: function() {
      if (!this.account || this.account === "" || !this.connected) {
        return "Connect wallet";
      } else {
        let length = this.account.length;
        return (
          this.account.substring(0, 6).toUpperCase() +
          "..." +
          this.account.substring(length - 4, length).toUpperCase()
        );
      }
    },
    isSmallScreen() {
      return window.innerWidth < 550;
    },
    ...mapGetters(["account", "connected", "accountVotes"])
  }
};
</script>

