<template>
  <div class="connect-button hidden-lg-and-up">
    <rect-button
      width="180px"
      class=""
      :backgroundColor="'#fe9612'"
      :color="'#fff'"
      @click="showWalletDialog = true"
    >
      {{ wallet }}
    </rect-button>
    <connect-dialog
      :showDialog="showWalletDialog"
      @onClose="showWalletDialog = false"
    />
  </div>
</template>

<script>
import RectButton from "./RectButton.vue";
import ConnectDialog from "./ConnectDialog.vue";
import ClipboardJS from "clipboard";
import { mapGetters } from "vuex";

export default {
  components: {
    RectButton,
    ConnectDialog
  },
  data() {
    return {
      showWalletDialog: false,
      copied: false
    };
  },
  mounted() {
    this.$store.dispatch("getAccount");
  },
  computed: {
    wallet: function() {
      if (!this.account || this.account === "" || !this.connected) {
        return "Connect wallet";
      } else {
        let length = this.account.length;
        return (
          this.account.substring(0, 6).toUpperCase() +
          "..." +
          this.account.substring(length - 4, length).toUpperCase()
        );
      }
    },
    isSmallScreen() {
      return window.innerWidth < 450;
    },
    ...mapGetters(["account", "connected"])
  },
  methods: {
    async copyAddress() {
      let clipboard = new ClipboardJS(".copy-btn");
      let that = this;
      clipboard.on("success", function(e) {
        console.debug(e);
        that.copied = true;
      });
      setTimeout(() => (this.copied = false), 5000);
    },
    async connectWallet() {
      this.showWalletDialog = false;
      await this.$store.dispatch("connectWallet");
    }
  }
};
</script>

<style lang="scss" scoped>
@import "../global.scss";

.connect-button {
  position: fixed;
  bottom: 30px;
  right: 1em;
}

/deep/ .btn {
  background-color: $accent-color;
  color: white;
  &:hover {
    background-color: $primary-color;
    color: white;
  }
}
</style>
